import React, { useEffect } from 'react';

import Section from '@components/Section';
import { Helmet } from 'react-helmet';
import styled from '@emotion/styled';
import mediaqueries from '@styles/media';

export interface IEzoicAds {
  id: number;
  slot: number;
}

const EzoicAds: React.FC<IEzoicAds> = ({ id }) => {
  useEffect(() => {
    try {
      var ezstandalone = window.ezstandalone || {};
      ezstandalone.cmd = ezstandalone.cmd || [];
      ezstandalone.cmd.push(function() {
        // If the placeholder ID’s change make sure to redefine, if not just call refresh
        ezstandalone.displayMore(id);
      });
    } catch (e) {
      console.error(e);
    }
  }, []);

  return (
    <Section narrow>
      <SubscriptionContainer>
        <Content>
          <div id={`ezoic-pub-ad-placeholder-${id}`}></div>
        </Content>
      </SubscriptionContainer>
    </Section>
  );
};

export default EzoicAds;

const SubscriptionContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0px auto 0px;
  z-index: 1;

  ${mediaqueries.tablet`
    padding: 50px 0 0;
    text-align: center;
  `}

  ${mediaqueries.phablet`
    margin: 20px auto 80px;
  `}
`;

const Content = styled.div`
  margin: 0 auto;
  width: 100%;
  margin-top: 30px;

  ${mediaqueries.tablet`
    h3 {
      padding: 0 50px;
    }
  `}

  ${mediaqueries.phone`
    h3 {
      padding: 0 24px;
    }
  `}
`;
